




import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';
import * as L from 'leaflet';
import 'leaflet-easybutton';
import { Comic, Panel } from '@/model';

import { PageViewer } from '@/leaflet-components/PageViewer';

@Component
export default class Reader extends Vue {
  private map: PageViewer | null = null;

  private comic: Comic | null = null;
  private comicId = '';
  private pageNumber = 0;

  async mounted() {
    this.comicId = this.$route.params.comicId;
    this.pageNumber = Number(this.$route.params.pageNumber);

    const comicResponse = await axios.create().get<Comic|string>(`/api/comic/${this.comicId}`);
    debugger;
    if (typeof comicResponse.data === 'string') {
      comicResponse.data = JSON.parse(comicResponse.data as string) as Comic;

    }
    this.comic = comicResponse.data as Comic;

    this.map = new PageViewer(this.$refs.map as HTMLElement);
    this.map.loadPage(this.comic, this.pageNumber);
  }
}
